.container {
  padding: 16px;
  display: flex;
  height: 100vh;
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-around;
}

.bg-black {
  background-color: black;
  border-radius: 4px;
  width: 100px;
  height: 100px;
}

.side-bar {
  width: 30%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: black;
  border-radius: 4px;
  padding: 32px;
  box-sizing: border-box;
}

.banner {
  display: flex;
  flex-direction: column;
  width: 65%;
  border-radius: 4px;
  overflow-y: scroll;
}

.about {
  line-height: 1.8;
  /* background-color: #e2e2e2; */
  padding: 16px;
  border-radius: 4px;
  margin-bottom: 16px;
}

.tag {
  background-color: black;
  border-radius: 8px;
  padding: 6px 10px;
  color: white;
  margin-left: 4px;
}

.dp {
  width: 50%;
  border-radius: 50%;
}

.edu-head {
  display: inline;
  margin-right: 4px;
}

.sub-head {
  color: #222;
  float: right;
}

.edu {
  line-height: 1.8;
  background-color: #e2e2e2;
  padding: 16px;
  border-radius: 4px;
  margin-bottom: 16px;
}

.job {
  margin: 0;
}

.sub-job {
  font-style: italic;
}

.links {
  padding: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.links > div {
  padding: 8px;
}

::-webkit-scrollbar {
  display: none;
}
